<template>
    <div class="page-container">
        <div v-if="articleViewShow">
            <articleViewComponent :params="viewParams" @viewHide="viewHide"></articleViewComponent>
        </div>
        <div id="search-container">
            <searchComponent :keyword="keyword" @articleView="articleView"></searchComponent>
        </div>
    </div>
</template>
<script>
    import searchComponent from "./component/search"
    import articleViewComponent from "./component/articleView"
    import JQ from "jquery";
    export default {
        name: 'Search',
        props: ["keyword"],
        components: {
            searchComponent,
            articleViewComponent
        },
        data() {
            return {
                articleViewShow:false,
                viewParams:{},
            }
        },
        created() {

        },
        methods: {
            articleView(params) {
                this.articleViewShow = true;
                this.viewParams = params;
                JQ('#search-container').hide();
            },
            viewHide() {
                this.articleViewShow = false;
                this.viewParams = {};
                JQ('#search-container').show();
            }
        }

    }
</script>

<style lang="scss" scoped>
    .page-container {
        padding:0;
        #search-container {

        }
    }
</style>