<template>
    <div class="search-container">
        <div class="search-header">
            <div class="category-layout pc-element">
                <div class="logo" v-if="selectShow">
                    <img src="../../../../assets/img/logo2.png">
                </div>
            </div>
            <div class="search-layout">
                <div class="search-box">
                    <form novalidate @submit.prevent="search()">
                        <div class="search-box-outline">
                            <div class="select-box pc-tablet-element" @click="showDetailSearch()">
                                <div class="text">상세검색</div>
                                <div class="icon">
                                    <font-awesome-icon icon="fa-solid fa-sort-down" v-if="selectShow"/>
                                    <font-awesome-icon class="up" icon="fa-solid fa-sort-up" v-else/>
                                </div>
                            </div>
                            <div class="search-input">
                                <div class="input">
                                    <input type="text" v-model="keywordString" class="search-input-text">
                                </div>
                                <div class="img-button">
                                    <button type="submit">
                                        <font-awesome-icon icon="fa-solid fa-magnifying-glass"/>
                                    </button>
                                </div>
                            </div>
                        </div>
                        <div class="search-error" v-if="errorMessage">
                            {{errorMessage}}
                        </div>
                    </form>
                </div>
                <div class="select-mobile-box mobile-element" @click="showDetailSearch()">
                    <div class="text">상세검색</div>
                    <div class="icon">
                        <font-awesome-icon icon="fa-solid fa-angle-down" v-if="selectShow"/>
                        <font-awesome-icon class="up" icon="fa-solid fa-angle-up" v-else/>
                    </div>
                </div>
                <div id="detail-part">
                    <ul class="tbl">
                        <li class="th">컨텐츠 분류</li>
                        <li class="select-radio boards-check" style="width:90%;">
                            <span class="check-span" v-for="item in imptBids">
                                <v-checkbox :value="item.bid" color="#000" :label="item.bname" dense v-model="boards"/>
                            </span>
                        </li>

                    </ul>
                    <ul class="tbl bottom-tbl">
                        <li class="th">검색범위</li>
                        <li class="select-radio">
                            <div class="radio-box">
                                <v-radio-group
                                        v-model="keywordCmd"
                                        dense
                                        row
                                >
                                    <v-radio
                                            color="#000"
                                            label="전체"
                                            value="all"
                                    ></v-radio>
                                    <v-radio
                                            color="#000"
                                            label="제목"
                                            value="subject"
                                    ></v-radio>
                                    <v-radio
                                            label="내용"
                                            color="#000"
                                            value="content"
                                    ></v-radio>
                                    <v-radio
                                            label="댓글"
                                            color="#000"
                                            value="comment"
                                    ></v-radio>
                                    <v-radio
                                            label="파일명"
                                            color="#000"
                                            value="file"
                                    ></v-radio>
                                </v-radio-group>
                            </div>
                        </li>
                        <li class="th pc-element" style="min-width:80px;">연산검색</li>
                        <li class="select-radio pc-element" style="width:50%;display:flex;">
                            <div class="radio-box">
                                <v-radio-group
                                        v-model="matchType"
                                        dense
                                        row
                                >
                                    <v-radio
                                            color="#000"
                                            label="AND"
                                            value="and"
                                    ></v-radio>
                                    <v-radio
                                            color="#000"
                                            label="OR"
                                            value="or"
                                    ></v-radio>
                                    <v-radio
                                            label="NOT"
                                            color="#000"
                                            value="not"
                                    ></v-radio>

                                </v-radio-group>
                            </div>
                            <div v-if="matchType=='not'" class="not-input-box">
                                <input type="text" v-model="notKeyword" class="not-input">
                                <v-btn class="not-button" depressed color="#000" @click="setNotKeyword()">
                                    <span>적용</span></v-btn>
                                <div class="search-error" v-if="notKeywordMessage">
                                    {{notKeywordMessage}}
                                </div>
                            </div>
                        </li>
                    </ul>
                    <ul class="tbl bottom-tbl tablet-mobile-element">
                        <li class="th" style="min-width:80px;">연산검색</li>
                        <li class="select-radio" style="display:flex;">
                            <div class="radio-box">
                                <v-radio-group
                                        v-model="matchType"
                                        dense
                                        row
                                >
                                    <v-radio
                                            color="#000"
                                            label="AND"
                                            value="and"
                                    ></v-radio>
                                    <v-radio
                                            color="#000"
                                            label="OR"
                                            value="or"
                                    ></v-radio>
                                    <v-radio
                                            label="NOT"
                                            color="#000"
                                            value="not"
                                    ></v-radio>

                                </v-radio-group>
                            </div>
                            <div v-if="matchType=='not'" class="not-input-box">
                                <input type="text" v-model="notKeyword" class="not-input">
                                <v-btn class="not-button" depressed color="#000" @click="setNotKeyword()">
                                    <span>적용</span></v-btn>
                                <div class="search-error" v-if="notKeywordMessage">
                                    {{notKeywordMessage}}
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <div class="search-body">

            <div class="category-layout pc-element">
                <div class="logo"></div>
                <div class="category-box">
                    <div class="category-title">
                        <font-awesome-icon class="icon" icon="fa-solid fa-magnifying-glass"/>
                        통합검색 분류
                    </div>
                    <div class="category-body">
                        <ul class="category-ul">
                            <li class="category-li" v-for="(menu,index) in menuList">
                                <div class="main-category" @click="mainMenuOpen(index)">
                                    <div v-if="menu.subList && menu.subList.length>0">
                                        <font-awesome-icon class="angle-icon" v-if="menu.open"
                                                           icon="fa-solid fa-angle-down"/>
                                        <font-awesome-icon class="angle-icon" v-else
                                                           icon="fa-solid fa-angle-right"/>
                                    </div>
                                    <font-awesome-icon class="icon" v-if="menu.open"
                                                       icon="fa-solid fa-folder-open"/>
                                    <font-awesome-icon class="icon" v-else icon="fa-solid fa-folder"/>
                                    <div class="category-name">{{menu.mname}} [{{util.numberFormat(menu.count)}}]
                                    </div>
                                </div>
                                <ul class="sub-category-ul"
                                    v-if="menu.subList && menu.subList.length>0 && menu.open">
                                    <li class="sub-category-li" v-for="(subMenu,subIndex) in menu.subList">
                                        <div :class="'sub-category '+subMenu.active" @click="goBoard(index,subIndex)">
                                            <!-- div v-if="subMenu.subCateList && subMenu.subCateList.length>0">
                                                <font-awesome-icon class="angle-icon" v-if="subMenu.open"
                                                                   icon="fa-solid fa-angle-down"/>
                                                <font-awesome-icon class="angle-icon" v-else
                                                                   icon="fa-solid fa-angle-right"/>
                                            </div-->
                                            <!--font-awesome-icon class="icon" v-if="subMenu.open"
                                                               icon="fa-solid fa-folder-open"/>
                                            <font-awesome-icon class="icon" v-else icon="fa-solid fa-folder"/ -->
                                            <font-awesome-icon class="icon" icon="fa-solid fa-folder"/>
                                            <div class="category-name">{{subMenu.mname}}
                                                [{{util.numberFormat(subMenu.count)}}]
                                            </div>
                                        </div>
                                        <!--ul class="cate-category-ul"
                                            v-if="subMenu.subCateList && subMenu.subCateList.length>0 && subMenu.open">
                                            <li :class="'cate-category-li '+cate.active"
                                                v-for="(cate,cateIndex) in subMenu.subCateList"
                                                @click="goCategory(index,subIndex,cateIndex)">
                                                <font-awesome-icon class="icon" icon="fa-solid fa-folder"/>
                                                <div class="category-name">{{cate.mname}}
                                                    [{{util.numberFormat(cate.count)}}]
                                                </div>
                                            </li>
                                        </ul-->

                                    </li>
                                </ul>
                            </li>
                        </ul>
                    </div>
                    <div class="hit-keyword-title">인기 검색어</div>
                    <ul class="hit-keyword">
                        <li @click="setKeyword(item.name)"  v-for="(item,index) in keywordList">
                           <div :class="item.active">{{index+1}}. &nbsp;
                            {{item.name}}</div>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="search-layout">
                <div class="list-container" v-if="searchResult">
                    <div class="search-result-total-box">
                        <div class="search-result-total">
                            <span>"{{keywordResult}}"</span>에 대한 통합 검색결과가 총 <span>{{total}}건</span> 조회되었습니다
                        </div>
                        <ul class="search-result-button-box">
                            <li v-for="sort in sortList">
                                <v-btn :class="'button '+sort.active" depressed @click="setSort(sort.code)"><span>{{sort.name}}</span>
                                </v-btn>
                            </li>
                        </ul>
                    </div>
                    <ul class="board-container" v-if="totalShow">
                        <li class="board-item">
                            <div class="board-name"  @click="totalOpenShow()">통합검색 [{{util.numberFormat(total)}}건]
                            </div>
                            <div v-if="totalOpen && articleList.length>0" class="bottom-line">
                                <ul class="article-container">
                                    <li v-for="(item,index) in articleList" @click="articleView(item)">
                                        <div class="article-data">
                                            <div class="subject">
                                                <span v-if="item.open=='no'">
                                                    <font-awesome-icon style="font-size:12px;color:#666"
                                                                       icon="fa-solid fa-lock"/>
                                                </span>
                                                <div class="subject-value" v-html="item.subject"></div>
                                            </div>
                                            <div class="desc" v-html="item.content"></div>
                                            <div class="keyword-box" v-if="item.keywords || item.file">
                                                <div class="keyword" v-if="item.keywords" v-html="item.keywords"></div>
                                                <div class="file" v-if="item.file">
                                                    <img src="../../../../assets/img/file.gif">
                                                    <span v-html="item.file"></span>
                                                </div>
                                            </div>
                                            <div class="author">
                                                작성자<span class="text">{{item.userName}}</span>
                                                작성일자<span class="text">{{item.createAt}}</span>
                                                <span v-if="item.subject">조회수<span
                                                        class="text">{{item.hit}}</span></span>
                                            </div>
                                        </div>
                                        <div class="img" v-if="item.imgUrl">
                                            <img :src="item.imgUrl">
                                        </div>
                                    </li>
                                </ul>
                                <paging :paging="paging" :reload="pagingReload" @pageMove="pageMode"></paging>
                            </div>
                        </li>
                    </ul>

                    <ul class="board-container list-board" v-if="boardList.length>0">
                        <li class="board-item" v-for="(board,index) in boardList" v-if="board.hide==false">
                            <div class="board-name" @click="boardSearch(board.bid,index)">{{board.name}}
                                [{{util.numberFormat(board.count)}}건]
                            </div>
                            <div class="list-bottom-line" v-if="board.articleList.length>0 && board.open">
                                <ul class="article-container">
                                    <li v-for="(item,index) in board.articleList" @click="articleView(item)">
                                        <div class="article-data">
                                            <div class="subject">
                                                <span v-if="item.open=='no'">
                                                     <font-awesome-icon style="font-size:12px;color:#666"
                                                                        icon="fa-solid fa-lock"/>
                                                </span>
                                                <div class="subject-value" v-html="item.subject"></div>
                                            </div>
                                            <div class="desc" v-html="item.content"></div>
                                            <div class="keyword-box" v-if="item.keywords || item.file">
                                                <div class="keyword" v-if="item.keywords" v-html="item.keywords"></div>
                                                <div class="file" v-if="item.file">
                                                    <img src="../../../../assets/img/file.gif">
                                                    <span v-html="item.file"></span>
                                                </div>
                                            </div>

                                            <div class="author">
                                                작성자<span class="text">{{item.userName}}</span>
                                                작성일자<span class="text">{{item.createAt}}</span>
                                                <span v-if="item.subject">조회수<span
                                                        class="text">{{item.hit}}</span></span>
                                            </div>
                                        </div>
                                        <div class="img" v-if="item.imgUrl">
                                            <img :src="item.imgUrl">
                                        </div>
                                    </li>
                                </ul>
                                <paging :paging="board.paging" :reload="board.pagingReload"
                                        @pageMove="pageBoardMode"></paging>
                            </div>
                        </li>
                    </ul>

                </div>
            </div>
        </div>
        <articleUpassComponent :bid="bid" :articleId="articleId" @upassSuccess="viewSuccess" :modelOpen="passDialogShow"
                               @parentOpenFalse="passDialogShow=false"></articleUpassComponent>
    </div>
</template>
<script>
    import apiService from '@/config/web/apiService'
    import {
        API_URL_GET_SEARCH,
        API_URL_GET_TOTAL_SEARCH,
        API_URL_GET_BID_SEARCH,
    } from '@/config/web/urls'
    import AUTH from '@/config/web/auth'
    import UTIL from '@/common/util'
    import {mapGetters} from "vuex";
    import paging from "@/components/paging";
    import articleUpassComponent from '@/components/articleUpass'
    import JQ from "jquery"

    export default {
        name: 'Search',
        props: ["keyword"],
        components: {
            paging,
            articleUpassComponent
        },
        computed: {
            ...mapGetters('config', ["getMenu"]),
        },
        watch: {
            keywordString(val) {
                if (val) {
                    this.errorMessage = '';
                } else {
                    this.errorMessage = '검색어를 입력하세요'
                }
            },
            notKeyword(val) {
                if (val) {
                    this.notKeywordMessage = '';
                } else {
                    this.notKeywordMessage = '검색어를 입력하세요'
                }
            },
            matchType(val) {
                if (val != 'not') {
                    this.keywordString = this.keywordString.replace(/NOT/gi, '');
                }
            }
        },
        data() {
            return {
                util: UTIL,
                boardIds: {},
                boardList: [],
                articleList: [],
                memberInfo: {},
                serverFlag: false,
                searchResult: false,
                selectShow: true,
                errorMessage: '',
                keywordString: '',
                menuList: [],
                boards: [],
                keywordCmd: 'all',
                total: 0,
                page: 1,
                limit: 10,
                searchType: 'and',
                lastFrm: {},
                totalOpen: true,
                paging: {},
                pagingReload: false,
                pageGroup: 10,
                bid: '',
                articleId: '',
                bidIndex: 0,
                matchType: 'and',
                notKeyword: '',
                notKeywordMessage: '',
                totalShow:true,
                orgString: '',
                passDialogShow: false,
                keywordList: [],
                categoryList: {},
                sort: 'default',
                sortList: [
                    {code: 'default', active: 'active', name: '정확도순'},
                    {code: 'createAt', active: '', name: '최신순'},
                    {code: 'hit', active: '', name: '조회수'}
                ],
                imptBids: [],
                firstInit: false,

            };
        },
        created() {
            const member = AUTH.getSession();
            if (member && member.memberInfo) {
                this.memberInfo = member.memberInfo;
            }
            this.keywordString = this.keyword;
            this.setInit();
            this.search();
        },
        methods: {
            setInit() {
                for (let menu of this.getMenu) {
                    if (menu.muse != 'yes') continue;
                    if (menu.subList && menu.subList.length > 0) {
                        for (let subMenu of menu.subList) {
                            if (subMenu.muse != 'yes') continue;
                            if (subMenu.linkType == 'board') {
                                this.boardIds[subMenu.boardId] = subMenu.mname;
                            }
                        }
                    } else {
                        if (menu.linkType == 'board') {
                            this.boardIds[menu.boardId] = menu.mname;
                        }
                    }
                }


            },
            setNotKeyword() {
                if (!this.notKeyword) {
                    this.notKeywordMessage = '검색어를 입력하세요'
                    return;
                }
                this.keywordString = this.keywordString + ' NOT ' + this.notKeyword;
            },
            setKeyword(name) {
                this.keywordString = name;
                this.search();

            },
            setSort(code) {
                if (code == this.sort) return;
                for (let item of this.sortList) {
                    if (item.code == code) item.active = 'active';
                    else item.active = '';
                }
                this.page = 1;
                this.sort = code;
                this.search()

            },
            showDetailSearch() {
                if (this.selectShow) {
                    this.selectShow = false;
                    JQ('#detail-part').slideUp(100);

                    this.boards = [];
                    this.keywordCmd = 'all';
                    this.matchType = 'and';

                } else {
                    this.selectShow = true;
                    JQ('#detail-part').slideDown(100);
                }

            },
            setCategory(boardList) {

                for (let menu of this.getMenu) {
                    if (menu.muse != 'yes') continue;
                    if (menu.subList && menu.subList.length > 0) {
                        let subMenus = [];
                        let subFlag = false;
                        for (let subMenu of menu.subList) {
                            if (subMenu.muse != 'yes') continue;
                            if (subMenu.linkType == 'board') {

                                if (boardList[subMenu.boardId].categorys) {
                                    subMenu.subCateList = [];
                                    let cateCodes = {};
                                    for (const cateKey in boardList[subMenu.boardId].categorys) {
                                        let cateItem = boardList[subMenu.boardId].categorys[cateKey]
                                        if (cateItem.use == 'yes') {
                                            cateCodes[cateItem.code] = cateItem.name;
                                            subMenu.subCateList.push({
                                                mname: cateItem.name,
                                                code: cateItem.code,
                                                active: '',
                                            })
                                        }
                                    }

                                }
                                subMenu.open = false;
                                subMenu.active = '';
                                subMenu.count = 0;
                                subMenus.push(subMenu);
                                subFlag = true;
                            }
                        }
                        if (subFlag) {
                            menu.subList = subMenus;
                            menu.open = true;
                            menu.count = 0;
                            this.menuList.push(menu);
                        }
                    } else {
                        if (menu.linkType == 'board') {
                            menu.open = false;
                            menu.count = 0;
                            menu.subList = [];
                            this.menuList.push(menu);
                        }
                    }
                }


            },
            mainMenuOpen(index) {
                let item = this.menuList[index];
                if (item.open) {
                    item.open = false;
                } else {
                    item.open = true;
                    /*
                     if (item.subList && item.subList.length > 0) {
                         for(let subItem of item.subList) {
                             if (!subItem.open) subItem.open = true;
                         }
                     }

                     */
                }
                this.$set(this.menuList, index, item);

            },
            goBoard(index, subIndex) {
                let item = this.menuList[index].subList[subIndex];
                /*
                if (item.subCateList && item.subCateList.length > 0) {
                    if (!item.open) {
                        item.open = true;
                        item.active = 'active';

                    } else {
                        item.open = false;
                        item.active = '';
                    }
                    let k = 0;
                    for (let cate of item.subCateList) {
                        if (!item.open) cate.active = ''
                        else cate.active = 'active'
                        this.$set(item.subCateList, k, cate);
                        k++;
                    }
                    //console.log(item.subCateList)
                    this.$set(this.menuList[index].subList, subIndex, item);
                } else {
                    if (item.count > 0) {
                        if (item.active) {
                            item.active = '';
                        } else {
                            item.active = 'active';
                        }
                    }


                }

                 */
                if (item.count > 0) {
                    if (item.active) {
                        item.active = '';
                    } else {
                        item.active = 'active';
                    }
                }
                this.categorySearch(item.boardId);

            },
            categorySearch(bid) {
                const boardIds = {};
                let boardCount = 0;
                this.categoryList = {};
                for (const menu of this.menuList) {
                    if (menu.subList && menu.subList.length > 0) {
                        for (const subMenu of menu.subList) {
                            if (subMenu.active) {
                                boardIds[subMenu.boardId] = subMenu.boardId;
                                boardCount++;
                                if (subMenu.subCateList && subMenu.subCateList.length > 0) {
                                    for (const cateItem of subMenu.subCateList) {
                                        if (cateItem.active) {
                                            this.categoryList[cateItem.code] = cateItem.mname;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }

                if (boardCount < 1) {
                    this.totalShow = true;
                    this.totalOpen = true;
                    for (const board of this.boardList) {
                        board.hide = false;
                        board.open = false;
                    }
                } else {
                    this.totalShow = false;
                    let index = 0;
                    let selectIndex = 0;
                    let check = false;
                    let boardInfo = {};
                    let tempBoardInfo = {};
                    let tempSelectIndex = 'none';
                    for (const board of this.boardList) {
                        if (boardIds[board.bid]) {
                            if(bid == board.bid) {
                                boardInfo = board;
                                check = true;
                                selectIndex = index;
                            } else {
                                if(tempSelectIndex == 'none') {
                                    tempBoardInfo = board;
                                    tempSelectIndex = index;
                                }
                            }
                            board.hide = false;
                        } else {
                            board.hide = true;
                        }
                        index++;
                    }
                    if(check) {
                        this.boardSearch(boardInfo.bid, selectIndex);
                    } else {
                        this.boardSearch(tempBoardInfo.bid, tempSelectIndex);
                    }
                }

            },
            goCategory(index, subIndex, cateIndex) {
                let items = this.menuList[index].subList[subIndex];
                let k = 0;
                let check = false;
                for (let item of items.subCateList) {
                    if (cateIndex == k) {
                        if (item.active) item.active = '';
                        else {
                            item.active = 'active';

                        }
                    }
                    if (item.active) {
                        check = true;
                    }
                    k++;
                }
                if (!check) {
                    items.active = '';
                    items.open = false;
                } else {
                    items.active = 'active';
                    items.open = true;
                }
                this.$set(this.menuList[index].subList, subIndex, items);


            },
            mainMenuChoice(index) {
                let item = this.menuList[index];
                if (item.subList && item.subList.length > 0) {
                    if (!item.open) item.open = true;

                }

            },
            setArticleList(list) {
                let articleList = [];
                for (let article of list) {
                    article.createAt = article.createAt.replace('T', ' ').substring(0, 16)
                    if (article.imgs) {
                        let images = article.imgs.split(',');
                        article.imgUrl = apiService.imgHost + 'api/controller/board/getArticleImage?imgName=' + encodeURIComponent(images[0])
                    }
                    if (article.searchDfileNames) {
                        let fileList = article.searchDfileNames.split(',');
                        let fileName = '';
                        for (let file of fileList) {
                            if (file.indexOf(this.keyword) !== -1 && !fileName) {
                                fileName = file.replace(file, '<em>' + this.keyword + '</em>');
                            }
                        }
                        article.file = fileName;
                    }

                    article.content = UTIL.stripTagsNotEm(article.content);
                    if (article.keywords) {
                        let keywords = article.keywords.split(',');
                        let keywordResults = [];
                        for (let keytext of keywords) {
                            if (keytext.indexOf(this.keyword) !== -1) {
                                keywordResults.push(keytext.replace(keytext, '<em>' + this.keyword + '</em>'))
                            }
                        }
                        if (keywordResults.length > 0) {
                            article.keywords = '#' + keywordResults.join(', #')
                        }

                    }
                    articleList.push(article)
                }
                return articleList;

            },
            /*
            *@ 초기 검색시 결과
            *
             */
            setDataList(datas) {
                this.totalShow = true;
                this.total = datas.totalData.total;
                this.articleList = this.setArticleList(datas.totalData.list)
                this.pagingReload = true;
                setTimeout(() => {
                    this.pagingReload = false;
                }, 100)
                this.paging = {
                    total: this.total,
                    currentPage: this.page,
                    limit: this.limit,
                    pageGroup: this.pageGroup,
                }

                this.totalOpen = true;
                for (let menu of this.menuList) {
                    if (menu.subList.length > 0) {
                        let count = 0;
                        for (let subMenu of menu.subList) {
                            subMenu.active = '';
                            subMenu.count = (datas.bidCount[subMenu.boardId]) ? parseInt(datas.bidCount[subMenu.boardId]) : 0;
                            count += subMenu.count;
                        }
                        menu.count = count;
                    } else {
                        menu.count = (datas.bidCount[menu.boardId]) ? parseInt(datas.bidCount[menu.boardId]) : 0;
                    }
                }
                this.boardList = [];
                for (const key in datas.bidCount) {

                    if (datas.bidCount[key] > 0) {
                        this.boardList.push({
                            name: this.boardIds[key],
                            count: datas.bidCount[key],
                            bid: key,
                            articleList: [],
                            open: false,
                            hide: false,
                            page: 1,
                        });
                    }

                }

            },
            setBidDataList(datas, index) {
                let articleList = this.setArticleList(datas.articleData.list)
                let item = this.boardList[index];
                item.open = true;
                item.articleList = articleList;
                item.pagingReload = true;
                item.paging = {
                    total: item.count,
                    currentPage: item.page,
                    limit: this.limit,
                    pageGroup: this.pageGroup,
                }
                this.$set(this.boardList, index, item);
                setTimeout(() => {
                    item.pagingReload = false;
                    this.$set(this.boardList, index, item);
                }, 100)

            },
            totalOpenShow() {
                this.totalOpen = true;
                for (let item of this.boardList) {
                    item.open = false;
                }

            },
            /*
            *@ 초기 검색
            *
             */
            search() {
                if (this.serverFlag) return;
                if (!this.keywordString) {
                    this.errorMessage = '검색어를 입력하세요'
                    return;
                }
                let searchBids = [];
                for (const key in this.boardIds) {
                    searchBids.push(key)
                }
                if (this.boards.length > 0) {
                    searchBids = [];
                    for (const bid of this.boards) {
                        searchBids.push(bid)
                    }
                }
                let getCategory = 'no'
                if (!this.firstInit) {
                    this.firstInit = true;
                    getCategory = 'yes'
                }

                this.page = 1;
                let frm = {
                    keyword: this.keywordString,
                    searchBids: searchBids,
                    keywordCmd: this.keywordCmd,
                    getCategory: getCategory,
                    page: this.page.toString(),
                    limit: this.limit.toString(),
                    matchType: this.matchType,
                    orderByField: this.sort
                }
                this.lastFrm = frm;
                this.$eventBus.$emit('overlay', 'open');
                this.serverFlag = true;
                apiService.post(API_URL_GET_SEARCH, frm, (res) => {
                    this.searchResult = true;
                    this.keywordResult = this.keywordString;
                    this.$eventBus.$emit('overlay', 'hide');
                    this.serverFlag = false;
                    if (res.status == 'success') {
                        if (getCategory == 'yes') {
                            let boardList = {}
                            if (res.categoryList) {
                                for (const brd of res.categoryList) {
                                    let categorys = {};
                                    if (brd.categoryUse == 'yes') {
                                        let categoryList = JSON.parse(brd.categoryList);
                                        for (const cate of categoryList) {
                                            categorys[cate.code] = cate;
                                        }
                                    }
                                    boardList[brd.bid] = {
                                        name: brd.bname,
                                        categorys: categorys,
                                    };
                                }
                            }
                            this.setCategory(boardList);
                        }
                        if (res.keywordList) {
                            res.keywordList.sort(function (a, b) {
                                if (parseInt(a.hits) > parseInt(b.hits)) return -1;
                                return 1;
                            })
                            this.keywordList = [];
                            for (let item of res.keywordList) {
                                if(this.keywordString == item.name) {
                                    item.active = 'active';
                                } else item.active = '';
                                this.keywordList.push(item)
                            }

                        }
                        this.imptBids = [];
                        for (const board of res.boardList) {
                            this.imptBids.push(board)
                        }
                        this.setDataList(res)

                    }
                });
            },
            totalSearchWithPage() {
                if (this.serverFlag) return;
                let searchBids = [];
                for (const key in this.boardIds) {
                    searchBids.push(key)
                }
                let frm = {
                    keyword: this.lastFrm.keyword,
                    keywordCmd: this.lastFrm.keywordCmd,
                    searchBids: searchBids,
                    page: this.page.toString(),
                    limit: this.limit.toString(),
                    matchType: this.lastFrm.matchType,
                    orderByField: this.lastFrm.orderByField
                }

                this.$eventBus.$emit('overlay', 'open');
                this.serverFlag = true;
                apiService.post(API_URL_GET_TOTAL_SEARCH, frm, (res) => {
                    this.$eventBus.$emit('overlay', 'hide');
                    this.serverFlag = false;
                    if (res.status == 'success') {
                        this.articleList = [];
                        this.articleList = this.setArticleList(res.totalData.list)

                        this.paging = {
                            total: this.total,
                            currentPage: this.page,
                            limit: this.limit,
                            pageGroup: this.pageGroup,
                        }
                        this.pagingReload = true;
                        setTimeout(() => {
                            this.pagingReload = false;
                        }, 100)


                    }
                });
            },
            boardSearch(bid, index) {
                if (this.serverFlag) return;
                this.bid = bid;
                this.bidIndex = index;
                if (this.boardList[index].open) return;
                if (this.boardList[index].articleList.length > 0) {
                    this.totalOpen = false;
                    for (let item of this.boardList) {
                        item.open = false;
                    }
                    this.boardList[index].open = true;
                    return;
                }
                let frm = {
                    keyword: this.lastFrm.keyword,
                    bid: bid,
                    keywordCmd: this.lastFrm.keywordCmd,
                    page: '1',
                    limit: this.limit.toString(),
                    matchType: this.lastFrm.matchType,
                    orderByField: this.lastFrm.orderByField
                }

                this.$eventBus.$emit('overlay', 'open');
                this.serverFlag = true;
                apiService.post(API_URL_GET_BID_SEARCH, frm, (res) => {
                    this.$eventBus.$emit('overlay', 'hide');
                    this.serverFlag = false;
                    if (res.status == 'success') {
                        this.totalOpen = false;
                        for (let item of this.boardList) {
                            item.open = false;
                        }
                        this.setBidDataList(res, index)
                    }
                });
            },
            boardSearchWithPage() {
                if (this.serverFlag) return;
                let frm = {
                    keyword: this.lastFrm.keyword,
                    bid: this.bid,
                    keywordCmd: this.lastFrm.keywordCmd,
                    page: this.boardList[this.bidIndex].page.toString(),
                    limit: this.limit.toString(),
                    matchType: this.lastFrm.matchType,
                    orderByField: this.lastFrm.orderByField
                }
                this.$eventBus.$emit('overlay', 'open');
                this.serverFlag = true;
                apiService.post(API_URL_GET_BID_SEARCH, frm, (res) => {
                    this.$eventBus.$emit('overlay', 'hide');
                    this.serverFlag = false;
                    if (res.status == 'success') {
                        this.setBidDataList(res, this.bidIndex)
                    }
                });
            },
            pageMode(params) {
                this.page = params.page;
                this.totalSearchWithPage();
            },
            pageBoardMode(params) {
                let item = this.boardList[this.bidIndex];
                item.page = params.page;
                this.$set(this.boardList, this.bidIndex, item)
                this.boardSearchWithPage();
            },
            boardAction(params) {
                switch (params.type) {
                    case 'view':
                        this.articelView(params.item)
                        break;
                }
            },
            articleView(item) {
                let viewFlag = false;
                if (item.open == 'no') {
                    if (this.memberInfo.role == 'ROLE_ADMIN') {
                        viewFlag = true;
                    } else {
                        if (item.userId) {
                            if (item.userId == this.memberInfo.uid) viewFlag = true;
                        } else {
                            this.articleId = item.id;
                            this.bid = item.bid;
                            this.passDialogShow = true;
                            return;
                        }
                    }
                } else {
                    viewFlag = true;
                }
                if (viewFlag) {
                    let id = item.id;
                    let commeintId = '';
                    if (item.parentId) {
                        id = item.parentId
                        commeintId = item.id;
                        sessionStorage.setItem('viewComment', item.id);
                    }
                    this.$emit('articleView', {bid: item.bid, id: id, commeintId: commeintId})
                }
            },
            viewSuccess() {
                let id = this.articleId;
                let commeintId = '';
                this.$emit('articleView', {bid: this.bid, id: id, commeintId: commeintId})

            },

        },
        updated() {

            JQ('.subject em').css({
                'color': '#C51162',
                'font-weight': 'bold',
                'font-size': '18px',
                'font-style': 'normal'
            });
            JQ('.desc em').css({
                'color': '#C51162',
                'font-weight': 'bold',
                'font-style': 'normal'
            });
            JQ('.keyword em').css({
                'color': '#C51162',
                'font-weight': 'bold',
                'font-style': 'normal'
            });
            JQ('.file em').css({
                'color': '#C51162',
                'font-weight': 'bold',
                'font-style': 'normal'
            });

        }
    }
</script>

